// import { Typography } from '@material-ui/core'
import React from 'react'
import { useLocation } from 'react-router-dom'
import useStyles from './OnboardingHeader.styles'
import IconLogo from '../../../../assets/Icons/IconLogo/IconLogo'
import { signOut } from '../../../../services/axios/auth'
import { useAppDispatch } from '../../../../services/store/hooks'

const OnboardingHeader = () => {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const location = useLocation()
  const inactiveItemStyle = classes.navText
  const activeItemStyle = `${classes.navText} ${classes.activeNavText}`

  return (
    <div className={classes.root}>
      <div className={classes.logo}>
        <IconLogo onClick={() => dispatch(signOut())} className={classes.hover} />
      </div>
      <ul className={classes.breadcrumbsContainer}>
        <li className={location.pathname.includes('/onboarding/personal-info') ? activeItemStyle : inactiveItemStyle}>
          1. Персональная информация
        </li>
        <li className={location.pathname.includes('/onboarding/experience') ? activeItemStyle : inactiveItemStyle}>
          2. Опыт работы и образование
        </li>
        <li className={location.pathname.includes('/onboarding/resume') ? activeItemStyle : inactiveItemStyle}>
          3. Резюме и портфолио
        </li>
      </ul>
    </div>
  )
}

export default OnboardingHeader
